"use client";
import * as CryptoJS from "crypto-js"; // Import crypto-js
import { useState } from "react";
import {Ai} from "@/app/components/ai";

// The correct password (hashed) you want to protect your ElevenLabs AI with
const correctPasswordHash = CryptoJS.SHA256("GambiinaJaMonster").toString(); // Replace with your actual password

const Home = () => {
    const [password, setPassword] = useState<string>("");
    const [message, setMessage] = useState<string>("");

    const handleLogin = () => {
        // Hash the entered password using SHA-256
        const enteredPasswordHash = CryptoJS.SHA256(password).toString();

        // Compare the entered hash with the stored hash
        if (enteredPasswordHash === correctPasswordHash) {
            setMessage("Access granted to ElevenLabs AI!");
            // Here you can either redirect to the AI page or load the AI directly
            // Example: window.location.href = "/ai"; or show a component with your AI interface
                <Ai/>
        } else {
            setMessage("Incorrect password. Access denied.");
        }
    };

    return (
        <div>
            <h1>Please type your beta key</h1>
            <div>
                <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Enter Key"
                />
                <button onClick={handleLogin}>Login</button>
            </div>
            <div>{message}</div>
        </div>
    );
};

export default Home;
